@import "_vars";

.TodoItem {
  background-color: #FAFAFA;
  color: var(--black);
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
  border-radius: 40px;
}

.TodoItem-p {
  margin: 20px 0 20px 20px;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
}
.TodoItem-p--complete {
  text-decoration: line-through;
}

.Icon {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 48px;
  width: 48px;
  font-size: 24px;
  font-weight: bold;
}

.Icon-check {
  position: absolute;
  left: 12px;
}
.Icon-check--active,.Icon-check:hover {
  fill: #4caf50;
}

.Icon-delete {
  position: absolute;
  right: 10px;
}
.Icon-delete svg:hover {
  fill: red;
}
