.ChangeAlert-bg {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #1e1e1f50;
  z-index: 2;
}
.ChangeAlert-container {
  position: fixed;
  bottom: -10px;
  left: -10px;
  right: -10px;
  padding: 34px;
  background-color: #1e1e1f;
  color: #f2f4f5;
  z-index: 2;
  text-align: center;
  font-size: 30px !important;
}
.ChangeAlert-container button {
  margin-top: 16px;
  margin-bottom: 16px;
  padding: 10px 20px;
  font-size: 20px;
  background: #5055a1d1;
  color: white;
  border-radius: 5px;
}
